            <div id="vqTest">
            <h4>VQ Test</h4>
            <p v-if="!user.vqTest.finished">Nije uradjen</p>
            <div class="" v-if="user.vqTest.finished">
              <p>Radjen: {{vqDate}}</p>
              <div class="vqPovucen" v-if="this.user.vqTest.result<=-15">
                <h3>"KV" prfil : "Pvun". ({{this.user.vqTest.result}})</h3>
                <p>V "vl z ivtm"   slb ili ptisnut. Vi nist zdvlni svtm ili ivtm tkvim kkvi jesu, li nist sklni d ih menjate. Gnrln s st ugrnim, dgim i ludim. Vi vlit d s pvut ng d se eksponirate. lit d vdit mirn ivt, dlk d svtskg mt. Nmt ni k l, ni k mbici. Drugi Vs mgu divti k psivn ili sttin. Bz inicitiv, nprduzimliv. K nkg k vi vli kntmplciu d kci. Vrvtn  V mt: "Nm ptrb gurti rku, n svkk t."</p>

              </div>
              <div class="vqOprezan"  v-if="(this.user.vqTest.result>-15)&&(this.user.vqTest.result<=-1)">
                <h3>"VK" Profil je: "Oprezan." ({{this.user.vqTest.result}})</h3>
                <p>Vasa "volja za zivotom" je u pripravnosti. Vi ste budni, posmatrate svet oko sebe i procenjujete potencijalni uticaj dogadjaja na Vas zivot. Spremni ste da reagujete ako je potrebno, ali niste skloni da se pokrenete i preuzmete inicijativu, ukoliko niste primorani na to. </p>
                <p>Pkuete d izbgn rizik i d s ztitit d nkivng. Puzdni ste k prfsinlc i k pritl, sv dk s n nt u kntrvrzn ili rizin situcii. Prihvtit pti trnd rzmilnj z ki prcnit d  rzumn. U principu vlit lud, li lit d prvrit d li su puzdni. Kd s s nkim pritn st, dt sv d sb d mu budt d pmi. Drugi Vs divlvu pr k nkg k prihvt incitivu, ng k inicitr. K nkg k  sdti n grdi dk n vidi u km prvcu vtr duv. K strtipng p ninu rzmilnj i k n b stimultivng. V mt  vrvtn: "Vi vlim d igrm n sigurn".</p>

              </div>
              <div class="vqAngazovan" v-if="(this.user.vqTest.result>=1)&&(this.user.vqTest.result<=14)">
                <h3>"VK" Profil je:"Angaovan". ({{this.user.vqTest.result}})</h3>
                <p>V "vl z ivtm"  ktivirn. Vi st "ktr". Pkr Vs svt. ivt  pun mgunsti k su i zbvn i krisn. lit d idt dl: d rdit stvri i d uivt u ivtu. Vi t inicirti nv prkt, kk u Vm prfsinlnm, tk i u Vm linm ivtu. Privui Vs ludi s kim ste d mt d rdit rzn stvri: prtnri, pr ng drugvi ili srdn du. Vlit pruzimnj dgvrnsti i n plit s d pvlivnj u vnsti i prihvatanja rizik. tviti st.</p>
                <p>Drugi ludi  Vs vrvtn divti k lidr, k nkg s idm i inicitivm, ktivng i puzdng, znimlivg. d  Vs divti i k prvi zuztg, k nkg k s n prd zbvi ili putnju, k nkg kg s n tiu lin snj ili prblmi drugih ludi, k gistu.</p>
                <p>V mt  vrvtn:"Hd d t urdim!"</p>
              </div>
              <div class="vqHiperaktivan" v-if="this.user.vqTest.result>=15" >
                <h3>"KV" prfil : "Hipr-ktivn". ({{this.user.vqTest.result}})</h3>
                <p>V "vl z ivtm"  grmn. Vi st "prduztnik", "sv". U sebi knstntn oseate pritisk. Vi s nikd n mt stvrn pustiti i k i kd uivt u zbvi, uvk i dl rzmilt  nkm pslu ki trb d bvit. rt biti ngvni u nkm prktu i morate imati oseaj d  stln irit svu "m z ivt". P Vm, "svt trb zgrbiti!" Vi st nstrplivi, nikd zdvlni, sim tkm krtkih trnutk pstignu. Ptrbn Vm  d vdit, d pruzimt dgvrnst, r u suprtnm mislit d s stvri n dviti brz ili dvln fiksn, dnsn n ii "n V nin". Drugi ludi su u sutini izvrici Vih pduhvt, oni trb d iznsu V prkt. Nist upt zintrsvni z njihv lini ivt. Vm st krtivni. d Vs m pstti vliki lidr, pd uslvm d n prtrut ...</p>
                <p>Drugi Vs uglvnm vid k hipr-ktivng, vrl mtvitg, uvk u pkrtu, invtivng. S mnj pzitivn strn, mgli bi Vs smtrti z prvi npdng, rgntng, grsivng, nprng, zpvdniki nstrng. K nikd zdvlng rdhlir, ki n shvt u mu su prv vrdnsti ivt. K nstlivg prm drugim ludim i bz ling ivt.</p>
                <p>V mt  vrvtn: "Vi  bl".</p>
              </div>
            </div>
            <img src="@/assets/goldWApp.svg" alt="">

            </div>
            <button @click="makePDFVQ">MakePDF</button>
