              </div>
              <div v-if="belbinNajnizi==2">
                <p><b>Osoba Od Akcije ({{user.belbinTest.OA}})</b> Oblikuje nain na koji se koriste potencijali grupe. Dri panju usmerenu na odreivanje ciljeva i prioriteta, vodi grupnu diskusiju i oblikuje rezultate grupnih aktivnosti. Odlike: neumoran, dominantan, ekstrovertan, impulsivan, lako se iznervira. eli brzo da doe do rezultata. Poseduje takmiarski duh, arogantan je, ali zahvaljujui njemu zadaci bivaju obavljeni.</p>
              </div>
              <div v-if="belbinNajnizi==3">
                <p><b>Osoba Sa Idejama ({{user.belbinTest.OI}})</b> Predlae nove ideje i strategije, pojedinano obrauje najvanije probleme, pokuava da nametne svoje sopstvene ideje, koristei grupni pristup problemu (na bazi konfrontacije). Odlike: dominantan, inteligentan, introvertan. Moe se desiti da se izgubi u detaljima. Takoe kritikuje tue ideje. to je problem vei, to je za njega vei i izazov da ga rei. Smatra da ideje mogu zvuati udno ili nemogue (u poetku). Oko sebe stvara auru genija. </p>
              </div>
              <div v-if="belbinNajnizi==4">
                <p><b>Osoba Za Kontakte ({{user.belbinTest.OK}})</b> Istrauje, analizira i sabira informacije o idejama, dostignuima i delatnostima van grupe. Ostvaruje veze sa ljudima, koje mogu biti korisne za grupu, sposoban je da vodi pregovore. Odlike: uravnoteen, dominantan, ekstrovertan. Podstie inovacije, dobar improvizator. Pomalo suzdran kada trai dobrobit za grupu. esto kae: Nove mogunosti bie stvorene na tuim grekama.</p>
              </div>
              <div v-if="belbinNajnizi==5">
                <p><b>Sudija ({{user.belbinTest.S}})</b> Analizira problem, procenjuje ideje i predloge. Zahvaljujui njemu grupa ne prenagljuje i donosi dobro promiljene odluke. Odlike: inteligentan, uravnoteen, introvertan. Najobjektivniji je, celovita linost, ne unosi emocije, voli da ima vremena za razmiljanje. Ponekad mu manjka entuzijazma, ali njegova smirenost doprinosi da grupa donosi odmerene odluke. </p>
              </div>
              <div v-if="belbinNajnizi==6">
                <p><b>ovek Tima ({{user.belbinTest.CT}})</b> Podrava lanove grupe, u sluaju greke uvek odrava visok moral grupe, sposoban je da izbegne konflikt, stvara timski duh, osnauje saradnju i komunikaciju u grupi. Odan je grupi. Odlike: ekstrovertan, uravnoteen, nema veliku potrebu za dominacijom i rivalstvom, saoseajan. Njegov doprinos moda nije ba sasvim jasan, ali njegova odanost i posveenost timu su na velikoj ceni. Ne voli da se konfrotira.</p>
              </div>
              <div v-if="belbinNajnizi==7">
                <p><b>Perfekcionista ({{user.belbinTest.P}})</b> Usmeren je na specifine akcije: privoenje zadataka kraju u okviru odreenog roka i obezbeivanje razultata najvieg kvaliteta. Moe biti teak u kontaktima, zbog toga to se gubi u detaljima koji nisu toliko vani za izvoenje zadatka. Uvek je svestan ciljeva. Odlike: teak, napet, introvertan, disciplinovan.</p>
              </div>




