    this.chartData.EKSTRAVERZIJA = this.E1 + this.E2 + this.E3 + this.E4 + this.E5 + this.E6;
    this.chartData.OTVORENOST = this.O1 + this.O2 + this.O3 + this.O4 + this.O5 + this.O6;
    this.chartData.UGODNOST = this.U1 + this.U2 + this.U3 + this.U4 + this.U5 + this.U6;
