    this.chartDataDetails.Matanje=this.user.testLicnosti.O1;
    this.chartDataDetails.Estetinost=this.user.testLicnosti.O2;
    this.chartDataDetails.Osjeaji=this.user.testLicnosti.O3;
    this.chartDataDetails.Postupci=this.user.testLicnosti.O4;
    this.chartDataDetails.Ideje=this.user.testLicnosti.O5;
    this.chartDataDetails.Vrijednosti=this.user.testLicnosti.O6;

