      this.dateExistsVQ();
    }
},
methods:{
  async makePDFVQ(){

    var downloading = document.getElementById("vqTest");
    var doc = new jsPDF("l","pt","a4");

    await html2canvas(downloading,{
      scale: 2,
      allowTaint : true,
      useCORS: true,
      width: 810,
      height: 560
    }).then((canvas)=>{
      doc.addImage(canvas.toDataURL("image/png"),'PNG',5,5,810, 560);
    });

    doc.save("Document.pdf");

  },
  makePDF360(){
    window.html2canvas=html2canvas;
    //p = portrait (moze i l za landscape)
    //pt za measurement
    //a4 za format
    var doc = new jsPDF("l","pt","a4");
    doc.html(document.querySelector("#feedbackPDF"),{
      callback: function(pdf){
        pdf.save("mypdf.pdf");
      }
    });
  },
  makePDFBelvin(){
    window.html2canvas=html2canvas;
    //p = portrait (moze i l za landscape)
    //pt za measurement
    //a4 za format
    var doc = new jsPDF("l","pt","a4");
    doc.html(document.querySelector("#belvin"),{
      callback: function(pdf){
        pdf.save("mypdf.pdf");
      }
    });
  },
  isMobile() {
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      return true
    } else {
      return false
    }
  },
  dateExistsVQ(){
    let str =this.user.vqTest.date.substring(0, 19) ;
    this.vqDate= "Datum: "+str.substring(0,4)+"."+str.substring(5,7)+"."+str.substring(8,10)+". Vreme:"+str.substring(11,19);
},
  dateExistsTestLicnosti(){
    let str=this.user.testLicnosti.date.substring(0, 19) ;
  this.testLicnostiDate= "Datum: "+str.substring(0,4)+"."+str.substring(5,7)+"."+str.substring(8,10)+". Vreme:"+str.substring(11,19);
},

},
}
